import {sendSms,updateCustInfo} from '@/common/api.js'
export default {
	onLoad(options) {
		this.custCode=options.custCode;
		if(options.phone){
			this.phone=options.phone;
			this.send(this.phone);
			this.hasPhone=true;
		}
		this.color=this.BASECOLOR;
	},
	filters:{
		phoneSercet(value){
			if(value)
				return value.substring(0,3)+'****'+value.substring(7,11);
			else return '';	
		}
	},
	data() {
		return {
			custCode:'',
			color:'',
			phone:'',
			sendedPhone:'',
			code:'',
			hasPhone:false,//是否已经绑定过手机号
			getCodeText:'发送',
			getCodeisWaiting:false,
			sended:false,
		};
	},
	methods:{
		timer(){
			
		},
		sendCode(phone){
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(phone)) ){
				this.$refs.notify.show("手机号格式不正确",this.COLORCONFIG.danger)
				return false; 
			}
			this.getCodeText="发送中"
			this.getCodeisWaiting=true;
			sendSms(phone).then(res=>{
				this.sended=true;
				this.sendedPhone=phone;
				this.setTimerOut();
			}).catch(e=>{
				this.$refs.notify.show("发送失败!",this.COLORCONFIG.danger)
				this.getCodeText="发送";
				this.getCodeisWaiting=false;
			})
		},
		save(){
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phone)) ){
				this.$refs.notify.show("手机号格式不正确",this.COLORCONFIG.danger)
				return false; 
			}
			if(!this.code){
				this.$refs.notify.show("请输入验证码",this.COLORCONFIG.danger)
				return;
			}
			
			updateCustInfo(this.custCode,'','',this.phone,this.code).then(res=>{
				uni.navigateBack();
			}).catch(e=>{
				this.$refs.notify.show("验证码校验失败",this.COLORCONFIG.danger)
				
			})
		},
		setTimerOut(){
			let holdTime = 60;
			this.Timer = setInterval(()=>{
				if(holdTime<=0){
					this.getCodeisWaiting = false;
					this.getCodeText = "发送"
					clearInterval(this.Timer);
					return ;
				}
				this.getCodeText = "重新获取("+holdTime+")"
				holdTime--;
				
			},1000)
		}
		
	}
}
